.header_content{
      width: 100%;
      height: 321px;
      background: var(--bg);
      border-bottom: var(--border);
      position: relative;
}

.header_banner,
.banner_content{
      width: 100%;
      height: 250px;
      position: relative;
      overflow: hidden;
}

.banner_content{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
}

.banner_content:before{
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.5);
      left: 0;
      top: 0;
}

.container.banner{
      display: flex;
      height: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      color: var(--light);
}

.container.banner h1,
.user_data h2{
      font-size: 4rem;
      display: inline-block;
      background: var(--gradient);
      fill: none;
      color: transparent;
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
}

.container.banner p{
      width: 70%;
      font-size: 1.6rem;
      text-align: center;
}

.btn_theme{
      width: 42px;
      height: 42px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 1.5rem;
      right: 1.5rem;
      background: none;
}

.btn_theme *{
      color: var(--light);
      font-size: 2rem;
}

.header_bottom{
      background: var(--bg-card);
      width: 100%;
      height: 70px;
      bottom: 0;
      left: 0;
}

.container.bottom{
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
}

.header_user{
      display: flex;
      height: 84px;
      padding: .4rem;
      background: var(--bg-card);
      border-radius: .4rem;
      position: absolute;
      top: -3rem;
      left: 0;
      gap: 1rem;
      border: var(--border);
}

.user_avatar{
      width: 74px;
      height: 74px;
      border-radius: .3rem;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_data{
      padding-right: 1rem;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      color: #b2b1b1;
}

.user_data h2{
      font-size: 2rem;
}

.user_data span{
      font-weight: 600;
      font-size: 2rem;
}

.user_data p{
      font-size: 1.2rem;
}

.header_social{
      display: flex;
      align-items: center;
      gap: 1rem;
}

