.wrapper_modal{
      position: fixed;
      width: 100%;
      height: 100vh;
      background: rgba(0,0,0,.8);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 1000;
      visibility:hidden;
      opacity: 0;
}

.wrapper_modal.show{
      animation: show_modal .4s ease-in-out forwards;
}

@keyframes show_modal{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.btn_close{
      width: 40px;
      height: 40px;
      background: rgba(255,255,255,.1);
      border-radius: 50%;
      position: absolute;
      top: 1.5rem;
      right: 1.5rem;
      z-index: 10;
}

.btn_close *{
      color: var(--light);
      font-size: 2rem;
}

.modal_inner{
      width: 80%;
      height: auto;
      transform: scale(1.4);
      opacity: 0;
}

.modal_inner.show{
      animation: show_image .3s .4s ease-in-out forwards;
}

@keyframes show_image{
      100%{
            transform: scale(1);
            opacity: 1;
      }
}

.modal_inner img{
      width: initial;
      max-width: 100%;
}