.wrapper_load{
      position: fixed;
      width: 100%;
      height: 100vh;
      background: var(--light);
      top: 0;
      left: 0;
      z-index: 1001;
      display: flex;
      align-items: center;
      justify-content: center;
}

.load_inner h1{
      display: flex;
      gap: .5rem;
      font-size: 2rem;
}

.load_inner h1 span{
      font-size: 1.6rem;
      position: relative;
      animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse{
      0%{
            transform: translateY(0);
      }
      15%{
            transform: translateY(-35px);
      }
      30%{
            transform: translateY(0);
      }
      100%{
            transform: translateY(0);
      }
}