@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&family=Roboto:wght@100;300;400;500;700;900&display=swap');

:root{
  /* fonts */
  --ft-1: 'Poppins', sans-serif;
  --ft-2: 'Roboto', sans-serif;

  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;

  /* theme light */
  --bg: #f8f8f8;
  --bg-card: #fff;
  --border: solid 1px rgba(0,0,0,.05);
  --box-shadow: 1px 1px 1px rgba(0,0,0,.05);

  /* colors */
  --light: #fff;
  --gradient: linear-gradient(90deg, #ffdb3a, #ff3da9);
  --bg-footer: #0c0c0c;
  --txt-footer: #404040;

  /* globals */
  font-family: var(--ft-2);
  font-size: 62.5%;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  outline: none;
  border: none;
  cursor: pointer;
}

.container{
  width: 1064px;
  max-width: 96%;
  height: auto;
  margin: 0 auto;
  position: relative;
}