.btn_social{
      width: 34px;
      height: 34px;
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .2rem;
}

.btn_social *{
      font-size: 1.6rem;
}

.facebook{
      background: var(--facebook);
}

.twitter{
      background: var(--twitter);
}

.instagram{
      background: var(--instagram);
}

.youtube{
      background: var(--youtube);
}

.tiktok{
      background: var(--tiktok);
}

.tumblr{
      background: var(--tumblr);
}

.discord{
      background: var(--discord);
}