.content{
      width: 100%;
      height: auto;
}
.gallery_content{
      padding: 5rem 0;
}

.container.gallery{
      display: flex;
      gap: 1rem;
      flex-wrap: wrap;
}

.card_gallery{
      width: calc(50% - .5rem);
      height: 348px;
      border: solid 1rem var(--bg-card);
      background: var(--bg-card);
      position: relative;
      box-shadow: var(--box-shadow);
      overflow: hidden;
}

.card_image{
      position: relative;
      width: 100%;
      height: 100%;
      background: var(--bg-card);
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.card_image img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      opacity: .8;
      transition: .2s;
}

.card_gallery:hover .card_image img{
      opacity: 1;
}

.footer_card{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 40px;
      background: rgba(0,0,0,.5);
      transform: translateY(100%);
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 0 1rem;
      transition: .2s;
}

.card_gallery:hover .footer_card{
      transform: translateY(0);
}

.footer_card span{
      color: var(--light);
      text-transform: uppercase;
      font-size: 1.3rem;
}

.btn_gallery{
      width: 40px;
      height: 40px;
      position: absolute;
      top: calc(50% - 20px);
      left: calc(50% - 20px);
      border-radius: 50%;
      background: rgba(0,0,0,.5);
      z-index: 10;
      opacity: 0;
      transform: scale(2);
      transition: .2s;
}

.card_gallery:hover .btn_gallery{
      opacity: 1;
      transform: scale(1);
}

.btn_gallery *{
      color: var(--light);
      font-size: 1.6rem;
}

.footer{
      width: 100%;
      height: 100px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: .5rem;
      background: var(--bg-footer);
      color: var(--txt-footer);
}

.footer *{
      font-size: 1.2rem;
}

.footer span{
      font-weight: 600;
}